import React, { FunctionComponent } from "react";
import { lazy } from "react";
import { withTranslation, WithTranslation } from "react-i18next";

const Container = lazy(() => import("../common/Container"));
const ContentBlock = lazy(() => import("../components/ContentBlock"));

const Lore: FunctionComponent<WithTranslation> = ({ t }) => {
  return (
    <Container>
      <ContentBlock
        type="right"
        title={t("loreIntroductionTitle")}
        content={t("loreIntroductionText")}
        icon={`./img/svg/lore.svg`}
        id="intro"
      />
      <ContentBlock
        type="left"
        title={t("lore1Title")}
        content={t("lore1Text")}
        icon={`./img/svg/lore1.svg`}
        id="lore1"
      />
      <ContentBlock
        type="right"
        title={t("lore2Title")}
        content={t("lore2Text")}
        icon={`./img/svg/lore2.svg`}
        id="lore2"
      />
      <ContentBlock
        type="left"
        title={t("loreConclusionTitle")}
        content={t("loreConclusionText")}
        icon={`./img/svg/lore3.svg`}
        id="conclusion"
      />
    </Container>
  );
};

export default withTranslation()(Lore);
